/* CUSTOM BUTTONS */
[class^="icon-"]:before, [class*=" icon-"]:before {
	font-family: FontAwesome;
	font-weight: normal;
	font-style: normal;
	display: inline-block;
	text-decoration: inherit;
}

.btn_img_small {
	-webkit-transform: scale(.75) translateY(7px) translateX(4px);
	        transform: scale(.75) translateY(7px) translateX(4px);
}
.btn_img_medium{
	-webkit-transform: scale(.65) translateY(8px);
	        transform: scale(.65) translateY(8px);
}
.btn_img_large{
	-webkit-transform: scale(.80) translateY(7px);
	        transform: scale(.80) translateY(7px);
}

