  	padding: 5px;
	min-width: 320px !important;
	min-height: 240px !important;
	width:  50%  !important;
	height: auto !important;
}

.medium-vid{
	margin: auto;
  	padding: 5px;
	min-width: 320px !important;
	min-height: 240px !important;
	width:  75%  !important;
	height: auto !important;
}

.large-vid{
	margin: auto;
  	padding: 5px;
	width:  90%  !important;
	height: auto !important;
}

