.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

//////
/* CUSTOM BUTTONS */
[class^="icon-"]:before, [class*=" icon-"]:before {
	font-family: FontAwesome;
	font-weight: normal;
	font-style: normal;
	display: inline-block;
	text-decoration: inherit;
}

.btn_img_small {
	transform: scale(.75) translateY(7px) translateX(4px);
}
.btn_img_medium{
	transform: scale(.65) translateY(8px);
}
.btn_img_large{
	transform: scale(.80) translateY(7px);
}

.icon-angle-small {
}
.icon-angle-medium {
}
.icon-angle-large {
}

.icon-angle {
	cursor: pointer;
	-webkit-box-flex: none;
	-moz-box-flex: none;
	-webkit-flex: none;
	-ms-flex: none;
	flex: none;
	width:  22px !important;
	height: 22px !important;
}

.playerCustomizedButton {
	width:  22px !important;
	height: 22px !important;
}

.videoContainer{
    width: 100% !important;
    height: 100% !important;
}

.video-js .vjs-tech {
	position: relative;
	height: inherit;
}

.player {
	width: 80%;
	margin-left: auto;
	margin-right: auto;
	background: black;
}

.small-vid{
	margin: auto;
  	padding: 5px;
	min-width: 320px !important;
	min-height: 240px !important;
	width:  50%  !important;
	height: auto !important;
}

.medium-vid{
	margin: auto;
  	padding: 5px;
	min-width: 320px !important;
	min-height: 240px !important;
	width:  75%  !important;
	height: auto !important;
}

.large-vid{
	margin: auto;
  	padding: 5px;
	width:  90%  !important;
	height: auto !important;
}
